<template>
  <content>
    <v-card-actions>
      <v-btn icon @click="getTab">
        <v-icon>mdi-plus</v-icon>
        <v-dialog width="500" v-model="addUser">
          <v-card>
            <v-card-title>
              Usuario
            </v-card-title>
            <v-container>
              <v-row>
                <v-col sm="12">
                  <v-text-field outlined label="Nombre"></v-text-field>
                </v-col>
                <v-col sm="12">
                  <v-text-field outlined label="Apellidos"></v-text-field>
                </v-col>
                <v-col sm="12">
                  <v-text-field outlined label="Correo"></v-text-field>
                </v-col>
                <v-col sm="12">
                  <v-select
                    outlined
                    v-model="permiso"
                    :items="permisos"
                    label="Seleccionar permisos"
                    multiple
                    hint="Permisos de informacion en el parque"
                    persistent-hint
                  ></v-select>
                </v-col>
              </v-row>
            </v-container>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="green darken-1" text @click="addUserAction">
                Agregar
              </v-btn>
              <v-btn color="red" @click="closeAction">Cancelar</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog width="500" v-model="addNave">
          <plusCard dialogs="1"></plusCard>
        </v-dialog>
      </v-btn>
    </v-card-actions>
    <v-card-text>
      <v-tabs v-model="tab">
        <v-tab>
          Usuarios
        </v-tab>
        <v-tab>
          Inquilinos
        </v-tab>
        <v-tab>
          Informacion
        </v-tab>
      </v-tabs>
      <v-tabs-items v-model="tab">
        <v-tab-item></v-tab-item>
        <v-tab-item></v-tab-item>
        <v-tab-item>Hola s</v-tab-item>
      </v-tabs-items>
    </v-card-text>
  </content>
</template>
<script>
import axios from "axios";
import plusCard from "../components/plusCard";
export default {
  name: "parque",
  data() {
    return {
      tab: 0,
      parque: [],
      addUser: null,
      addNave:null,
      permisos: ["Eliminar", "Editar", "Agregar"],
      
    };
  },
  beforeMount() {
    let params = new URLSearchParams();
    params.append("id", this.$store.state.parque);
    axios
      .post(`${this.$store.state.url}/getpark`, params)
      .then((res) => {
        this.parque = res.data;
      })
      .catch((e) => console.log(e));
  },
  methods: {
    getTab() {
      switch (this.tab) {
        case 0:
          this.addUser = true;
        break;
        case 1:
          this.addNave = true
      }
    },
    closeAction() {
      this.addUser = false;
      this.addNave = false;
    },
    addUserAction() {},
  },
  components: {plusCard}
};
</script>
